import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import { hideModal, showModal } from "../../../actions/datamaster_action.jsx";
import {
  getDataUser,
  ShowModalTambahUser,
} from "../../../actions/mageuser_action.jsx";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import {
  NotifError,
  NotifInfo,
  NotifSucces,
} from "../../../components/helpers/notification.jsx";
import { deleteData, postData, putData } from "../../../config/axios.jsx";
import { reset } from "redux-form";
import ModalGlobal from "../../../components/helpers/ModalGlobal.jsx";
import FormDataUser  from "./FormDataUser.jsx"

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;

const maptostate = (state) => {
  return {
    DataUsers: state.manageusers.getDataUser,
    hideModal: state.datamaster.modalDialog,
  };
};

const hapusDataUser = (params, dispatch) => {
  Swal.fire({
    // title: "Anda Yakin !!",
    // text: "Ingin Menghapus Data Ini ?",
    html:
      "Apakah Anda Yakin Ingin " +
      "Menghapus " +
      "<h1><b>User Id " +
      params +
      "</b> ?</h1>",
    icon: "warning",
    position: "top-center",
    cancelButtonText: "Tidak",
    showCancelButton: true,
    confirmButtonText: "OK",
    showConfirmButton: true,
  }).then((result) => {
    if (result.isConfirmed) {
      deleteData("users/delete/id/" + params, {
        data: {
          user_id: params,
        },
      }).then(() => {
        NotifSucces("Data Berhasil Di Hapus").then(() =>
          dispatch(getDataUser())
        );
      });
    }
  });
};
class DataUsers extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      isLoading: false,
      columns: [
        {
          dataField: "user_id",
          text: "User Id ",
          sort: true,
        },
        {
          dataField: "usere_name",
          text: "User Name",
        },
        {
          dataField: "level",
          text: "Level",
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {
            let dataEdit = {
              user_id: row.user_id,
              usere_name: row.usere_name,
              level: row.level,
            };
             
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    onClick={() => this.formEditBaki(dataEdit)}
                    className="btn btn-primary mr-3"
                  >
                    <i className="fa fa-edit"></i>
                  </button>
                  <button
                    onClick={() =>
                      hapusDataUser(row.user_id, this.props.dispatch)
                    }
                    className="btn btn-danger"
                  >
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
      //   DataBaki: [
      //     {
      //       user_id: "TK",
      //       usere_name: "CCKL",
      //       level: "KALUNG MUDA",
      //     },
      //   ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    this.props.dispatch(getDataUser());
  }
  handleSubmit(data) {
    this.setState({ isLoading: true });
    if (data.level === undefined) {
      NotifInfo("Silahkan Pilih Status");
      return false;
    }
    if (data.level === 3) {
      NotifInfo("Silahkan Pilih Status");
      return false;
    }
    let data_edit = {
      user_name: data.usere_name,
      level: data.level,
      // password_lama: data.password_lama,
      // password: data.password,
    };
    let data_post = {
      user_id: data.user_id,
      user_name: data.usere_name,
      level: data.level,
      password: data.password,
      retype_password: data.password,
    }
    this.state.isEdit
      ? putData("users/update/id/" + data.user_id, data_edit)
          .then(() => {
            this.props.dispatch(hideModal());
          })
          // .then()
        .then(() => {
            NotifSucces("Data Berhasil Di Edit")
            this.props.dispatch(getDataUser());
          })
          .then(() => {
            this.props.dispatch(reset("ModalTambahUsers"));
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .catch((error) => {
            this.setState({
              isLoading: false
            })
            NotifError(error.response === undefined ? "Terjadi Kesalahan Saat Menyimpan Data" :  /e11000/i.test(error.response.data) ? "User Id Sudah Terdaftar" : error.response.data)
          })
      : postData("users/add/user", data_post)
          .then(() => {
            this.props.dispatch(hideModal());
          })
          // .then(NotifSucces("Data Berhasil Di Simpan"))
        .then(() => {
          NotifSucces("Data Berhasil Di Simpan")
            this.props.dispatch(getDataUser());
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .then(() => {
            this.props.dispatch(reset("ModalTambahUsers"));
          })
          .catch((error) => {
            this.setState({
              isLoading: false
            })
            NotifError(error.response === undefined ? "Terjadi Kesalahan Saat Menyimpan Data" :  /e11000/i.test(error.response.data) ? "User Id Sudah Terdaftar" : error.response.data)
            // NotifError(error.response === undefined ? "Terjadi Kesalahan Saat Menyimpan Data" : error.response.data)
          });
  }
  formEditBaki(dataEdit) {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalTambahUser(dataEdit));
    this.setState({
      isEdit: true,
    });
  }
  ShowModalTambahUser() {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalTambahUser(false));
    this.setState({
      isEdit: false,
    });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Manage Users</Link>
          </li>
          <li className="breadcrumb-item active">Data Users</li>
        </ol>
        <h1 className="page-header">Data Users </h1>
        <Panel>
          <PanelHeader>Data Users</PanelHeader>
          <br />
          <div className="container">
            <ToolkitProvider
              keyField="user_id"
              data={this.props.DataUsers || []}
              columns={this.state.columns}
              search
              exportCSV={{
                fileName: "Export Data Users.csv",
              }}
            >
              {(props) => (
                <div className="row">
                  <div className="col-6">
                    <button
                      onClick={() => this.ShowModalTambahUser()}
                      className="btn btn-primary"
                    >
                      {" "}
                      <i className="fa fa-plus"></i>{" "}
                    </button>
                  </div>
                  <div className="col-6">
                    <div className="text-right">
                      <SearchBar {...props.searchProps} />
                    </div>
                  </div>
                  <hr />
                  <div className="col-12">
                    <BootstrapTable
                      pagination={paginationFactory()}
                      {...props.baseProps}
                    />
                    <br />
                  </div>
                  <div className="col-6">
                    <ExportCSVButton {...props.csvProps}>
                        Export CSV!!
                      </ExportCSVButton>
                  </div>
                </div>
              )}
            </ToolkitProvider>
          </div>
          <br />
          {/* End Data Users */}
          <ModalGlobal
            title={this.state.isEdit ? "Edit Data Users" : "Tambah Data Users"}
            size="P"
            content={
              <FormDataUser
                isLoading={this.state.isLoading}
                isEdit={this.state.isEdit}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            }
          />

          {/* End Tambah Data Users  */}
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate, null)(DataUsers);
