import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { hideModal } from "../../../actions/datamaster_action";
import { ReanderField, ReanderSelect } from "../../../components/helpers/field";
import ValidasiDataUsers from "../../../Validasi/ManageUsers/ValidasiDataUsers";
import Loading from 'react-fullscreen-loading';

const options = [
  { value: "ADMIN", name: "ADMIN" },
  { value: "OWN", name: "OWNER" },
  { value: "KASIR", name: "KASIR" },
  { value: "SPV", name: "SUPERVISOR" },
];
const maptostate = (state) => {
  if (state.manageusers.ShowModalTambahUser !== null) {
    return {
      initialValues: {
        user_id: state.manageusers.ShowModalTambahUser.user_id,
        usere_name: state.manageusers.ShowModalTambahUser.usere_name,
        level: state.manageusers.ShowModalTambahUser.level,
        // password: state.manageusers.ShowModalTambahUser.password,
        // retype_password: state.manageusers.ShowModalTambahUser.retype_password,
      },
    };
  }
};
class FormTambahUser extends Component {
  state = {};
  componentDidMount() {
    setTimeout(() => {
      this.props.isEdit
        ? document.getElementById("usere_name").focus()
        : document.getElementById("user_id").focus();
    }, 100);
  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        autoComplete="off"
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
      >
        <Field
          id="user_id"
          label="User Id"
          name="user_id"
          nouperCase
          readOnly={this.props.isEdit}
          placeholder={"Silahkan Masukan User Id"}
          component={ReanderField}
        />
        <Field
          id="usere_name"
          name="usere_name"
          component={ReanderField}
          type="text"
          nouperCase
          label="Username"
          placeholder="Masukan Username"
        />
        <div className="form-group">
          <Field
            label="Level"
            name="level"
            options={options}
            value={this.props.level}
            // disabled={this.props.isEdit}
            placeholder={"Silahkan Pilih Level"}
            component={ReanderSelect}
          />
        </div>
        {!this.props.isEdit ? (
          <>
            <Field
              name="password"
              component={ReanderField}
              type="password"
              label="Password"
              nouperCase
              placeholder="Masukan Passsword"
            />
          </>
        ) : null}
        <div className="row">
          <div className="col-12 text-right">
            <button
              className="btn btn-primary"
              disabled={this.props.submitting}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
            &nbsp;
            <button
              className="btn btn-white"
              onClick={() => this.props.dispatch(hideModal())}
              type="button"
              disabled={this.props.isLoading}
            >
              Batal
            </button>
          </div>
        </div>
        {this.props.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </form>
    );
  }
}

FormTambahUser = reduxForm({
  form: "ModalTambahUsers",
  enableReinitialize: true,
  validate: ValidasiDataUsers,
})(FormTambahUser);
export default connect(maptostate, null)(FormTambahUser);
